import uniIcon from "@/components/uni-icon.vue"
import uniNumberBox from "../../components/uni-number-box.vue"
export default {
	components: {
		uniNumberBox,
		uniIcon,
	},
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			actived: 0, // 0:租赁流程;1属性参数:
			goodsImgsList: [
				{
					url: '../../static/images/img01.png'
				},
				{
					url: '../../static/images/img01.png'
				}
			],
			processList: [
				{
					icon: '../../static/images/tab-user.png',
					text: '选择设备和租期'
				},
				{
					icon: '../../static/images/tab-user.png',
					text: '在线预付租金（货按月代扣租金）'
				},
				{
					icon: '../../static/images/tab-user.png',
					text: '提交订单，系统审核，工作人员电话联系'
				},
				{
					icon: '../../static/images/tab-user.png',
					text: '发货安装，上门调试'
				}
			],
			showPopupBottom: false,
			typeList: ['￥3180/首年', '￥6260/2年', '￥9240/3年'],
			dateStart: currentDate,
			dateEnd: currentDate,
			typeActived: 0,
			selectNumber: 1,
			reserveNumber: 10,
		}
	},
	onLoad() {

	},
	methods: {
		toggleParam(index) {
			this.actived = index
		},
		selectType(index){
			this.typeActived = index
		},
		toggleSelectOption(status) {
			this.showPopupBottom = status
		},
		getDate() {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		bindStartDateChange(e) {
			this.dateStart = e.target.value
		},
		bindEndDateChange(e) {
			this.dateEnd = e.target.value
		},
		pageToOrder() {
			uni.navigateTo({ url: 'order' });
		}
	}
}
